.container {
    margin: 0;
    padding: 0;
    display: flex;
    min-height: calc(100vh - 56px);
    background-color: black;
}

h1 {
    color: darkseagreen;
    margin: 0;
    font-size: 60px;
    padding: 0 0 0 5%;
    font-family: "Share Tech Mono", serif;
    background-color: black;
}

.scrambleContainer {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 56px);
}

.picturesContainer {
    display: flex;
    flex: 1;
    height: calc(100vh - 56px);
}

.particles {
    position: absolute;
    width: 100%;
    height: 100%
}